<template>
  <div class="map-container">
    <l-map style="height: 500px; width: 900px" :zoom="zoom" :center="[`${$route.meta.centerlat}`, `${$route.meta.centerlon}`]">
      <l-tile-layer :url="url"></l-tile-layer>
      <ul v-for="restaurant in restaurants"  :key="restaurant.id">
        <li v-if="restaurant.location === `${$route.meta.filterByCity}`">
          <l-marker :lat-lng="[`${restaurant.lat}`,`${restaurant.lon}`]" >
            <l-popup class="popup">
              {{restaurant.name}}
              <br>
              <a :href="`https://www.google.com/maps/dir//${restaurant.lat},${restaurant.lon}/data=!3m1!4b1!4m2!4m1!3e3`">Direções</a>
            </l-popup>
          </l-marker>
        </li>
      </ul>
    </l-map>
  </div>
</template>

<script>
import db from '../components/firebaseInit'
import {LMap, LTileLayer, LMarker, LPopup } from 'vue2-leaflet';

export default {
    name: 'LeafletMap',
    components: {
        LMap,
        LTileLayer,
        LMarker,
        LPopup,
    },
    data: function() {
      return  {
            restaurants: [],
            restaurant_id: null,
            name: null,
            location: null,
            obs: null,
            price: null,
            zomato_link: null,
            lat: null,
            lon: null,
            map: null,
            tileLayer: null,
            url: 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/rastertiles/voyager/{z}/{x}/{y}.png',
            zoom: 14,
            }
          },

  created(){
    db.collection('restaurants').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        //console.log(doc.data());
        const data = {
          'id': doc.id,
          'restaurant_id': doc.data().restaurant_id,
          'name': doc.data().name,
          'location': doc.data().location,
          'obs': doc.data().obs,
          'price': doc.data().price,
          'zomato_link': doc.data().zomato_link,
          'lat': doc.data().lat,
          'lon': doc.data().lon
        }
        this.restaurants.push(data)
      })
    })
  },
  beforeRouteEnter(to, from, next){
    db.collection('restaurants').where('name', '==', to.params.name).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.name = doc.data().name
          vm.location = doc.data().location
          vm.price = doc.data().price
          vm.zomato_link = doc.data().zomato_link
          vm.obs = doc.data().obs
          vm.lat = doc.data().lat
          vm.lon = doc.data().lon
        })
      })
    })
  },
    mounted() {
      //this.initMap();
    },

    methods: {

      fetchData () {
        db.collection('restaurants').where('name', '==', this.$route.params.name).get().then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.name = doc.data().name
            this.location = doc.data().location
            this.price = doc.data().price
          })
        })
      },
      initMap() {
        this.map = L.map('map').setView([38.72, -9.145], 14);
        this.tileLayer = L.tileLayer(
          'https://cartodb-basemaps-{s}.global.ssl.fastly.net/rastertiles/voyager/{z}/{x}/{y}.png',
          {
            maxZoom: 18,
            attribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>, &copy; <a href="https://carto.com/attribution">CARTO</a>',
          }
        );
        this.tileLayer.addTo(this.map);
        this.placeMarker();

      },
      placeMarker(){
        this.marker = L.marker([38.71, -9.14]).addTo(this.map).bindPopup("Cervejaria Trindade").addTo(this.map);
        this.marker = L.marker([38.717424, -9.134936]).addTo(this.map).bindPopup("Clandestino").addTo(this.map);
        this.marker = L.marker([38.718717, -9.164987]).addTo(this.map).bindPopup("Moules & Beer").addTo(this.map);
        this.marker = L.marker([38.709588, -9.135050]).addTo(this.map).bindPopup("Tasca Kome").addTo(this.map);
        this.marker = L.marker([38.723471, -9.154331]).addTo(this.map).bindPopup("Forno d'Oro").addTo(this.map);
        this.marker = L.marker([38.711644, -9.141586]).addTo(this.map).bindPopup("Boa-Bao").addTo(this.map);
        this.marker = L.marker([38.705853, -9.157711]).addTo(this.map).bindPopup("Nogueiras").addTo(this.map);
      },
    },
  }
</script>

<style lang="scss">
.leaflet-popup-content-wrapper{
  width: 120px;
}
.popup{
  color: teal;
  font-size: 14px;
  font-weight: bold;
}
.map-container{
  display: flex;
  max-width: 95%;
}
.map{
  height: 500px;
  width: 700px;
}
l-map {
  height: 500px;
  width: 700px;
}
</style>



